import Layout from "../components/MyLayout";

const buttonStyle = {
  color: "white",
  backgroundColor: "green",
  padding: "0.5rem",
  borderRadius: "10px",
  fontSize: "1.5rem"
};

const inputStyle = {
  padding: "0.5rem 0",
  width: "85%",
  maxWidth: "60%",
  margin: "0.3rem auto",
  borderRadius: "2px",
  fontSize: "1.5rem"
};

class FirstForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      firstName: "",
      lastName: "",
      parentType: ""
    };

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(event) {
    this.setState({
      firstName: event.target.firstName,
      lastName: event.target.lastName,
      parentType: event.target.parentType
    });
  }

  handleSubmit(event) {
    alert(
      "A parent was registered: " +
        this.state.firstName +
        " " +
        this.state.lastName +
        " and is a " +
        this.state.parentType
    );
    event.preventDefault();
  }

  render() {
    return (
      <Layout>
        <form onSubmit={this.handleSubmit}>
          <div>
            <label htmlFor="firstName">First Name </label>
            <input
              style={inputStyle}
              id="firstName"
              type="text"
              placeholder="John"
              value={this.state.firstName}
              onChange={this.handleChange}
            />
          </div>
          <div>
            <label htmlFor="lastName">Last Name </label>
            <input
              style={inputStyle}
              id="lastName"
              type="text"
              placeholder="Smith"
              value={this.state.lastName}
              onChange={this.handleChange}
            />
          </div>
          <div>
            <label htmlFor="parent">Parent </label>
            <input
              style={inputStyle}
              id="parent"
              type="text"
              value={this.state.parentType}
              onChange={this.handleChange}
            />
          </div>
          <div>
            <button style={buttonStyle} type="submit" value="Submit">
              Submit
            </button>
          </div>
        </form>
      </Layout>
    );
  }
}

export default FirstForm;
